.nav {
  padding-top: 85px;
  color: #fff;
  text-transform: uppercase;

  &__list {
    display: flex;
    justify-content: space-between;
    align-items: center;
    list-style: none;
  }

  &__item {
    padding: 8px 24px;

    &--active {
      background-color: rgba(255, 255, 255, 0.1);
      border-radius: 8px;
    }
  }
  
  &__link {
    line-height: 16px;
    letter-spacing: 2px;
    text-decoration: none;
    transition: color $hover-duration;

    color: #fff;

    &:hover {
      color: $active-color;
    }
  }
}

@media screen 
  and (min-width: $adaptive-breakpoint)
  and (max-width: $standard-breakpoint) {
  .nav {
    padding-top: 40px;
    &__list {
      display: flex;
      justify-content: start;
      flex-wrap: wrap;
    }
  }
}

@media screen and (max-width: $adaptive-breakpoint) {
  .nav {
    display: none;
  }
}
